<template>
    <div class="container">
        <div class="content">
            <h2>
                {{ titleList }}
            </h2>
        </div>
        <div class="row">
            <div class="col-12 col-sm-6 col-md-4 mt-3" v-for="post in allPosts" :key="post.id">
                <div class="card">
                    <div class="card-content">
                        <div class="content">
                            <h4>
                                {{ post.title }}
                            </h4>
                            <p>
                                {{ textlimit(post.content) + ' .....'}}
                            </p>
                            <small>{{ post.created_at.seconds | moment("DD/MM/YYYY, h:mm a") }}</small>
                        </div>
                    </div>
                    <footer class="card-footer">
                        <router-link :to="'/' + post.post_id" class="card-footer-item">Read More</router-link>
                    </footer>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
    export default{
        props:{
            'allPosts': Array,
            'titleList': String
        },
        methods:{
          textlimit(text){
              return text.substring(0,120)
          },
        },
    }
</script>

