import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// Buefy Framework
import Buefy from 'buefy'
import 'buefy/dist/buefy.css'
Vue.use(Buefy)

// Format Date
Vue.use(require('vue-moment'));


Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
